body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*,::after,::before{margin:0;padding:0;box-sizing:border-box;font-family:"Noto Sans KR", sans-serif}body{background-color:#444}body{height:100vh;background:linear-gradient(to top, rgba(0,0,0,0.8), rgba(0,0,0,0.6)),url("https://images.unsplash.com/photo-1542435503-956c469947f6?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=967&q=80") center center no-repeat;background-size:cover;position:relative;overflow:hidden;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;z-index:0}@media screen and (max-width: 768px){body{-webkit-align-items:flex-end;align-items:flex-end;padding-bottom:3rem}}.container{z-index:10}.container .container__overlay{z-index:-1;position:fixed;background:#333;top:0;left:55%;-webkit-transform:rotate(15deg) translateX(0%);transform:rotate(15deg) translateX(0%);height:150%;width:150%}.review{height:50vh;width:40vw;padding:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.review__title{position:absolute;top:10%;color:#fff;border-bottom:1px solid #fff;font-size:2rem;font-weight:400}.review .image__container{position:absolute;top:20%;right:27%;height:300px;width:300px}.review .image__container img{object-fit:cover;height:100%;width:100%;border-radius:50%;border-right:20px solid #b3b3b3}@media screen and (max-width: 1024px){.review .image__container{height:250px;width:250px;right:15%}}@media screen and (max-width: 768px){.review .image__container{height:200px;width:200px;top:10%;left:40%}}@media screen and (max-width: 480px){.review .image__container{left:28%}}@media screen and (max-width: 1024px){.review{height:50vh;width:50vw}}@media screen and (max-width: 768px){.review{height:50vh;width:400px}}.reviewer{-webkit-transform:translateX(-50%);transform:translateX(-50%);padding:2rem;color:#fff;border-radius:10px;font-size:2rem}.reviewer .reviewer__name{text-align:center;font-size:2rem;text-transform:capitalize;font-weight:400}@media screen and (max-width: 768px){.reviewer .reviewer__name{font-size:1.5rem}}.reviewer .reviewer__title{text-align:center;font-size:1.8rem;text-transform:capitalize;font-weight:300}@media screen and (max-width: 768px){.reviewer .reviewer__title{font-size:1.3rem}}@media screen and (max-width: 1024px){.reviewer{-webkit-transform:translateX(-30%);transform:translateX(-30%)}}@media screen and (max-width: 768px){.reviewer{-webkit-transform:translateX(0%);transform:translateX(0%)}}.quote,.review__quote .quote-left,.review__quote .quote-right{position:absolute}@media screen and (max-width: 768px){.quote,.review__quote .quote-left,.review__quote .quote-right{font-size:1.3rem}}.review__quote{padding:4rem 2rem;position:relative}.review__quote .quote-left{top:0;left:0}.review__quote .quote-right{bottom:0;right:0}.review__quote .reviewer__quote{text-align:center;margin-top:1rem;font-size:1.2rem;font-weight:100}@media screen and (max-width: 768px){.review__quote .reviewer__quote{margin-top:0rem}}@media screen and (max-width: 768px){.review__quote{padding:2rem 1.5rem}}.btn,.prev__btn,.next__btn{position:absolute;border:none;background:none;color:#fff;font-size:2rem;cursor:pointer;font-weight:100;top:70%;outline:none}@media screen and (max-width: 768px){.btn,.prev__btn,.next__btn{font-size:1.5rem}}@media screen and (max-width: 480px){.btn,.prev__btn,.next__btn{font-size:1.2rem;top:90%}}.prev__btn{left:5%}.next__btn{right:10%}

